import { useEffect, useRef } from "react";
import { Button } from "./ui/button";
import { useSocket } from "@/context/SocketContext";

export default function Viewer() {
  const videoRef = useRef(null);
  const socket = useSocket();
  const pc = useRef(null);

  const connectToServer = async () => {
    pc.current = new RTCPeerConnection();
    const offer = await pc.current.createOffer();
    await pc.current.setLocalDescription(offer);
    socket.emit("rtc-offer", {sdp: pc.current.localDescription.sdp, type: pc.current.localDescription.type, userId: "user_2z3ZIFoH4iVg21CqsgbH8dSmaxM", deviceId: "desktop_001"});

    socket.on("answer", async (data) => {
      console.log("Received answer");
      await pc.current.setRemoteDescription(new RTCSessionDescription({sdp: data.sdp, type: data.type}));
    });

    // When remote media stream arrives
    pc.current.ontrack = (event) => {
      console.log("Track received");
      if (videoRef.current) {
        videoRef.current.srcObject = event.streams[0];
      }
    };

    // ICE candidate from server
    socket.on("ice-candidate", (candidate) => {
      pc.current.addIceCandidate(new RTCIceCandidate(candidate));
    });

    // Send ICE candidates to server
    pc.current.onicecandidate = (event) => {
      if (event.candidate) {
        socket.emit("ice-candidate", {data: event.candidate, userId: "user_2z3ZIFoH4iVg21CqsgbH8dSmaxM", deviceId: "desktop_001"});
      }
    };

    return () => {
      // Clean up on unmount
      if (pc.current) {
        pc.current.close();
      }
      if (socket.current) {
        socket.current.disconnect();
      }
    };
  };

  return (
    <div style={{ padding: "1rem" }}>
      <h2>Live Stream</h2>
      <Button onClick={connectToServer}>Connect</Button>
      <video
        ref={videoRef}
        autoPlay
        playsInline
        controls={false}
        style={{ width: "100%", backgroundColor: "#000" }}
      />
    </div>
  );
}
